import React from 'react'

const sizes = {
    little: {
        weight: '1,8 - 2 кг',
        portions: '6 - 8',
        price: '3800 руб'
    },
    middle: {
        weight: '2 - 2,5 кг',
        portions: '8 - 10',
        price: '4300 руб'
    },
    big: {
        weight: '3 - 3,25 кг',
        portions: '12 - 14',
        price: '5300 руб'
    }
}

class Cake extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            cake: sizes.little
        }
    }

    render() {
        const cake = this.state.cake

        return (
            <div className="catalog__item">
                <div className="catalog__image">
                    <img src={this.props.image} alt="cake" />
                </div>
                <h3>{this.props.title}</h3>
                <div className="catalog__description">
                    <ul>
                        {this.props.ingridients.map((ingridient) => {
                            return <li>{ingridient}</li>
                        })}
                    </ul>
                </div>
                <div className="catalog__buttons">
                    <div className={cake === sizes.little ? 'catalog__button active' : 'catalog__button'} onClick={() => this.setState({ cake: sizes.little })}>16см</div>
                    <div className={cake === sizes.middle ? 'catalog__button active' : 'catalog__button'} onClick={() => this.setState({ cake: sizes.middle })}>18см</div>
                    <div className={cake === sizes.big ? 'catalog__button active' : 'catalog__button'} onClick={() => this.setState({ cake: sizes.big })}>20см</div>
                </div>
                <div className="catalog__weight">
                    Вес: <span>{cake.weight}</span>
                </div>
                <div className="catalog__portions">
                    Порций: <span>{cake.portions}</span>
                </div>
                <div className="catalog__price">
                    Стоимость без декора: <span>{cake.price}</span>
                </div>
            </div>
        )
    }
}

export default Cake
